/**
 * @module plagiarism_turnitinsim/inbox_eula_launch
 */

define(['jquery',
        'core/templates',
        'core/modal_factory',
        'plagiarism_turnitinsim/modal_eula'
    ],
    function($, Templates, ModalFactory, ModalTcEula) {
        return {
            inboxEulaLaunch: function() {
                var trigger = $('.eula-row-launch');

                ModalFactory.create(
                    {
                        type: ModalTcEula.TYPE
                    },
                    trigger
                );
            }
        };
    }
);